.Disclaimer {
  background-color: black;
  color: white;
  text-align: center;
  padding: 15px;
  font-family: Verdana, sans-serif;
  font-size: 11px;

  @media screen and (min-width: 800px) {
    font-weight: bold;
    font-size: 14px;
    font-family: Roboto, sans-serif;
  }
}

.Disclaimer-link {
  opacity: 0.8;
  color: white;
  display: inline-block;

  &:hover {
    opacity: 1;
  }
}



